import React from 'react'
import PropTypes from 'prop-types'

export default function Navbar(props) {
  

    return (
        <div className="App fs-5">
         <nav className={`navbar navbar-expand-lg navbar-${props.mode} bg-${props.mode}`}>
         <div className="container-fluid">
             <a className="navbar-brand fw-bolder fs-4" href="#">{props.title}</a>
             <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent"
                 aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                 <span className="navbar-toggler-icon"></span>
             </button>
             <div className="collapse navbar-collapse" id="navbarSupportedContent">
                 <ul className="navbar-nav me-auto mb-2 mb-lg-0">
                     <li className="nav-item">
                         <a className="nav-link active" aria-current="page" href="#">Home</a>
                     </li>
                     <li className="nav-item">
                         <a className="nav-link" href="#">About us</a>
                     </li>
                 </ul>
             </div>
             <div className="form-check form-switch mt-3 px-4 fw-bolder">
                        <input className="form-check-input" onClick={props.toggleMode}  type="checkbox" id="flexSwitchCheckDefault"  />
                        <label className={`form-check-lable text-${props.mode==='light'?'dark':'light'}`}  htmlFor="flexSwitchCheckDefault">Dark Mode</label>
                    </div>
         </div>
         
     </nav>
     <div className="  container-fluid">
        <h1 className = {`bg-secondary  container-fluid text-${props.mode==='light'?'dark':'light'} text-center font-monospace bg-${props.mode}`}>Welcome to <b className=" fst-italic">TextUtils</b></h1>
    </div>
    
     {/* <hr/> */}
      
     </div>
     
    )
}

Navbar.propTypes={
    title:PropTypes.string,
    // aboutText:PropTypes.string
}